  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .analysisBox {
    text-align: left;
    padding: 0 15px;
    font-size: 14px;
    overflow: hidden;
    &>div {
      float: left;
      .ivu-radio-group {
        .ivu-radio-wrapper {
          margin-left: 10px;
          border-radius: 4px;
          &:first-child {
            margin-left: 0;
          }
          &.ivu-radio-wrapper-checked {
            background: #2d8cf0;
            color: #fff;
          }
        }
      }
    }
    &>p {
      float: right;
      span {
        margin-right: 20px;
        i {
          color: #ff0000;
          font-style: normal;
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
  .statistical{
    position: relative;
    width: 100%;
    height:calc(100% - 95px);
    .sta_box{
      position: absolute;
      height: 50%;
      width: 50%;
      padding:15px 20px;
      .con_box{
        height: 100%;
        width: 100%;
        background: #fff;
        border-radius: 10px;
        box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
        position: relative;
        overflow: hidden;
        .boxBtn{
          position: absolute;
          right: 10px;
          top:10px;
          z-index: 80;
        }
        .bigBox{
          width:calc(100vw - 60px);
          height:100%;
          .smallBox{
            width:50%;
            height:100%;
            float: left;
          }
        }
        .smallToBig {
          .smallBox {
            width: 100%;
          }
        }
      }
    }
    /* box放大 */
    .activeBox{
      left:0!important;
      top:0!important;
      padding: 15px !important;
      width: 100%;
      height: 100%;
      z-index: 99;
      transition: all 0.8s ease;
    }
  }
}
