























































































































































.query-box{
  height: 50px;
  border-bottom: 1px solid #d3d3d3;
  margin: 0 15px 15px;
  .query-box-title{
    line-height: 50px;
    font-size: 14px;
    overflow: hidden;
    padding: 0 0 10px;
    display: flex;
    justify-content: space-between;
    .queryTerm{
      margin-left: 10px;
      strong{
        margin-right: 10px;
        font-weight: normal;
      }
      span{
        padding:2px 10px;
        background: #61A7F3;
        border-radius: 4px;
        color:#fff;
        margin-left: 5px;
        margin-right: 25px;
        position: relative;
      }
    }
    .search-title {
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../../../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .search-items {
      flex-grow: 1;
      padding: 0 5px;
      text-align: left
    }
  }
  .query-down{
    position: absolute;
    top: 50px;
    left: 5px;
    right: 5px;
    background: white;
    box-shadow: 3px 3px 5px rgba(0,0,0,.2);
    border: 1px solid #f5f5f5;
    border-radius: 6px;
    z-index: 99999999;
    padding: 10px 20px;
    .queryTerm{
      padding:10px 5px;
      text-align: left;
      .textTerm {
        span {
          width: 60px;
        }
      }
    }
  }
}
.blue-block{
  display: inline-block;
  background: #61a7f3;
  color: white;
  padding: 0 10px;
  border-radius: 3px;
  margin-right: 15px;
}
