<template>
  <div id="mapbox" v-on:keyup.122 = "fullClose" v-on:keyup.27 = "fullClose">
    <div id="viewDiv"></div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="right">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="right">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
  </div>
</template>

<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer } from '@/utils/layers'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'
import collSign3 from '../../../assets/boundaryMarker_images/coll_bm3.png'
import collBm3 from '../../../assets/boundaryMarker_images/coll_sign3.png'
import { staticPoint } from '../../../api/dataAnalysis'

const markerSymbol = {
  type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
  url: collSign3,
  height: 16,
  width: 16,
  outline: { // autocasts as new SimpleLineSymbol()
    color: [255, 255, 255],
    width: 1
  }
}
export default {
  name: 'OnePiece',
  data () {
    return {
      map: null, // 地图实例
      view: null, // 地图视图实例
      type: ''
    }
  },
  props: ['boxstate', 'obj'],
  methods: {
    /**
     * @Description: 初始化“一张图”数据中心的地图
     * @author 张淼
     * @date 2019/4/16
     */
    async createMap () {
      // 导入需要的arcgis模块
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => {
        return res
      })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => {
        return res
      })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => {
        return res
      })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => {
        return res
      })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 6
      })
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-left')
      // 添加全屏
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 增加地图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.padding = {top: 20, left: 20, right: 55, bottom: 20}
      this.view.ui.add(basemapToggle, 'top-right')
      // 地图创建成功后调用任务斑块接口
      this.view.on('layerview-create', (event) => {
        this.getTaskList()
      })
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
      }
    },
    // 获取核查任务分布
    async getTaskList (obj) {
      await staticPoint({
        data: {
          cl001: obj ? obj.cl001 : '',
          cl010: obj ? obj.taskYear : '',
          adminRegionId: obj ? obj.code : ''
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          console.log(res)
          this.data = res.data.data
          // 添加核查任务分布
          this.layerAddData(this.data)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * 核查任务斑块分布,将获取到的数据添加到图层中
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    /* 将获取到的数据添加到图层中 */
    async layerAddData (data) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let addtaskLayer
      if (this.map.findLayerById('taskLayer')) {
        this.map.findLayerById('taskLayer').removeAll()
        this.map.findLayerById('taskLayer').visible = true
        addtaskLayer = this.map.findLayerById('taskLayer')
      } else {
        addtaskLayer = new GraphicsLayer({
          id: 'taskLayer'
        })
      }
      let Graphic = await arcgisPackage.Graphic
      // 封装面积增加的graphic数组
      data.forEach(val => {
        if (val.geometry) {
          // 绘制多边形
          let polygon = {
            type: 'polygon', // autocasts as Polygon
            rings: JSON.parse(val.geometry),
            spatialReference: 4326
          }
          let graphic = new Graphic({
            geometry: polygon,
            symbol: {
              type: 'simple-fill', // autocasts as SimpleFillSymbol
              color: [51, 133, 255, 0.5],
              style: 'solid',
              outline: {
                color: [51, 133, 255, 0.8],
                width: 1
              }
            }
          })
          addtaskLayer.add(graphic)
        }
      })
      this.map.add(addtaskLayer)
    }
  },
  async mounted () {
    // 初始化地图
    await this.createMap()
  }
}
</script>

<style lang="scss" scoped>
  #mapbox{
    height: 100%;
    display: flex;
    position: relative;
  }
  #layersControl{
    .layersControl_box{
      padding:3px;
      background: #fff;
      border: 1px solid #d1d1d1;
      .fraggableBox{
        padding: 0;
        .fraggableLi{
          min-width: 100px;
          padding: 5px 8px;
          border-top: 1px solid #d1d1d1;
        }
      }
    }
  }
  #viewDiv{
    height: 100%;
    flex-grow: 2;
  }
  #maptab{
    width: 300px;
    .firstmenu{
      >li{
        margin-bottom: 6px;
        text-align: left;
        >ul{
          >li{
            padding-left: 20px;
          }
        }
        >p{
          line-height: 40px;
          text-align: left;
          font-size: 16px;
          background-color: rgb(42,177,245);
          color: white;
          padding-left: 10px;
          border-radius: 4px;
          cursor: pointer;
          i{
            font-size: 16px;
            line-height: 40px;
            margin-right: 10px;
          }
        }
        .secondmenu{
          label{
            line-height: 30px;
            font-size: 14px;
          }
        }
      }
    }
  }
  #basemapToggle{
    border: 5px #000 solid;
  }
</style>
<style scoped>
  #maptab >>> .ivu-tabs-content{
    height: calc(100% - 40px);
    margin-top: -16px;
  }
  #maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
    padding: 16px;
    border: 1px solid #dcdee2;
    border-radius: 0 0 4px 4px;
    border-top: none;
  }
  #maptab >>> .ivu-tabs-nav{
    display: flex;
    float: none;
  }
  #maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
    flex-grow: 2;
    margin-right: 0;
  }
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
